.shadow-left{
  position: relative;
  &::before{
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 50%;
    display: block;
    position: absolute;
    z-index: 1;
    background: rgb(255,255,255);
    background: linear-gradient(0deg, rgba(255,255,255,0) 80%, rgba(0,0,0,1) 100%);
