.title {
  color: #fff;
  font-size:  var(--fz-xl);;
  margin-bottom: 100px;
  position: relative;
  &::after{
      content: '';
      position: absolute;
      top: -55px;
      left: 0;
      border: 1px solid var(--c-primary);
      width: 80px;
  }       
}

.tranformation{
  transform: translateY(100000px);
}


















.tab{
  position: relative;
  overflow: hidden;
  background: var(--c-secondary);
  color: #fff
}
 .tab:after {
  content: " ";
  top:0;
  width: 0%;
  height: 100%;
  background: #fff;
  position: absolute;
  transition: all 0.4s ease-in-out;
  right: 0;
 }
 
 .tab:hover::after {
  right: auto;
  left: 0;
  width: 100%;
 }
 
 .tab span {
  width: 100%;
  color: #fff;
  z-index: 20;
  transition: all 0.3s ease-in-out;
 }
 
 .tab:hover span {
  color: var(--c-secondary) !important;
 }
 















 
